.c-stats-viewer {
  padding: 0 10px;
  min-width: 200px; }
  .c-stats-viewer--grid {
    table-layout: fixed;
    width: 100%;
    text-align: center; }
    .c-stats-viewer--grid thead {
      font-size: 1.3rem;
      font-weight: 700; }
    .c-stats-viewer--grid__player {
      white-space: nowrap;
      text-overflow: ellipsis;
      overflow-x: hidden; }

.c-share-chart h3 {
  margin-bottom: 0.3em; }

.c-dealer-token {
  display: inline-block;
  font-size: 1rem;
  font-weight: 400;
  margin: 0 10px;
  padding: 0 5px;
  white-space: nowrap;
  background-color: lightgray;
  border-radius: 10px;
  transition: transform ease-in-out 0.25s; }
  .c-dealer-token__arrow {
    transition: opacity ease-in-out 0.25s; }
  .c-dealer-token--p1 {
    transform: translate(-20px, 0); }
    .c-dealer-token--p1 .c-dealer-token__arrow--p2 {
      opacity: 0; }
  .c-dealer-token--p2 {
    transform: translate(20px, 0); }
    .c-dealer-token--p2 .c-dealer-token__arrow--p1 {
      opacity: 0; }
