.c-set-summary--final {
  display: flex;
  justify-content: space-between;
  font-size: 2rem;
  padding: 6px 3px; }
  .c-set-summary--final__winner {
    flex: 1 1 auto;
    text-overflow: ellipsis;
    white-space: nowrap;
    overflow: hidden; }
  .c-set-summary--final__points {
    flex: 0 0 auto; }

.c-set-summary__player {
  display: flex;
  justify-content: space-between;
  padding: 3px; }
  .c-set-summary__player + .c-set-summary__player {
    border-top: 1px solid; }
