.c-match-viewer {
  height: 100%;
  max-width: 100vw;
  display: grid;
  grid-template-rows: [sets-start] minmax(0, 1fr) [sets-end input-start] auto [input-end];
  grid-template-columns: [sets-start input-start] 100% [sets-end input-end]; }
  .c-match-viewer__main {
    display: flex; }
  .c-match-viewer__sets {
    display: flex;
    flex-grow: 1;
    justify-content: space-around;
    align-items: flex-start;
    grid-area: sets; }
  .c-match-viewer .c-score-column {
    flex-shrink: 0; }
  .c-match-viewer__add-game {
    grid-area: input;
    padding: 20px; }
  .c-match-viewer .c-set-view {
    margin: 15px 10px; }

@media (max-width: 900px) {
  .c-match-viewer__sets {
    min-width: 900px; } }

@media (min-width: 1000px) {
  .c-match-viewer {
    grid-template-columns: [sets-start] 1fr [sets-end input-start] auto [input-end];
    grid-template-rows: [sets-start input-start] auto [sets-end input-end]; } }

.c-drawer .c-gameform {
  margin-left: 10px; }
