:root {
  --accent1-color: rgb(0, 81, 61);
  --primary-interaction-color: rgb(36, 123, 160);
  --selection-color: lightblue;
  --focus-border-color: var(--primary-interaction-color); }

html,
body,
#root {
  height: 100vh;
  width: 100vw;
  overflow: hidden; }

body {
  position: fixed;
  background-color: var(--accent1-color);
  margin: 0; }

#root {
  background-color: white; }

* {
  box-sizing: border-box;
  font-family: Futura, 'Segoe UI', --apple-system, sans-serif; }
